/* Existing Styles */

/* Remove or adjust conflicting styles from Create React App default */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Custom Styles */

/* Main App Container */
.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* Game Container */
.game-container {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 800px; /* Maximum size you want the game to be */
  max-height: 600px;
  margin: 0 auto;
  transform: scale(calc(100vw / 800)); /* Dynamically scale down based on viewport width */
  transform-origin: top; /* Scale from the top of the game */
  overflow: hidden;
}

.game-content {
  flex-grow: 1; /* Allow this section to grow */
  overflow-y: auto; /* Ensure it scrolls if content overflows */
  padding-bottom: 0; /* Remove bottom padding if there's unnecessary space */
}


/* Game Title */
.game-title {
  font-family: 'Libre Baskerville', serif;
  font-size: 30px; /* Reduced font size */
  margin: 20px 0 10px; /* Reduced margin */
  text-align: center;
}

/* Real News Headline */
.real-news h2 {
  font-family: 'Libre Baskerville', serif; /* Match title with classic font */
  font-size: 28px;
  margin-bottom: 20px;
  text-align: center;
}

/* Start Screen */
.start-screen {
  text-align: center;
  margin-top: 50px;
}

.start-screen h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.start-screen p {
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 1.5;
}

/* Timer Display */
.timer {
  position: fixed;
  top: 10px;
  right: 10px;
  font-size: 18px;
}

/* Word Cloud Container */
.word-cloud-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  overflow: hidden;
  box-sizing: border-box;
}

/* Submit Container */
.submit-container {
  text-align: center;
  margin-top: 10px;
}

/* "Tries Left" Display */
.submit-container div {
  font-size: 16px;
  margin-bottom: 5px;
}

/* Word Cloud */
.word-cloud {
  flex-shrink: 0;
  overflow: auto;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Word Styling */
.word {
  padding: 6px 8px;
  margin: 3px;
  border-radius: 12px;
  cursor: grab;
  display: inline-block;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  transition: all 0.2s ease;
  background-color: #007bff;
  font-size: 14px;
  line-height: normal;
  vertical-align: middle;
}

.word:active {
  cursor: grabbing;
  transform: scale(1.1);
}

/* Disable text selection for word cloud and analogies */
.word-cloud,
.analogies {
  user-select: none;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none;    /* Firefox */
  -ms-user-select: none;     /* IE/Edge */
}

/* Analogy Section */
.analogies {
  flex: 1;
  overflow: auto;
  padding: 10px;
}

.analogies p {
  font-size: 14px;
  line-height: 1.4;
  margin: 0;
  padding: 0;
}

/* Analogy Box Styling */
.analogy-box {
  border: 2px solid #007bff;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 15px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Gap Styling */
.gap {
  min-width: 60px;
  padding: 6px 8px;
  border-radius: 4px;
  color: white;
  display: inline-block;
  text-align: center;
  margin: 0 3px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-size: 14px;
  line-height: normal;
  vertical-align: middle;
}

.gap.correct {
  background-color: #28a745; /* Green */
}

.gap.wrong-place {
  background-color: #fd7e14; /* Orange */
}

.gap.incorrect {
  background-color: #dc3545; /* Red */
}

.gap.unused {
  background-color: #007bff; /* Blue */
}

.gap:hover {
  transform: scale(1.05);
}

/* Satirical Column */
.satirical-column {
  margin-top: 30px;
}

.satirical-column h2 {
  font-family: 'Libre Baskerville', serif;
  font-size: 32px;
  text-align: center;
  margin-bottom: 20px;
}

.satirical-column p {
  font-size: 18px;
  line-height: 1.6;
  text-align: justify;
  margin-bottom: 1em;
}

/* Buttons */
button {
  margin-top: 10px;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

button:hover:not(:disabled) {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Share Button Container */
.share-button-container {
  text-align: center;
  margin: 20px 0;
}

.share-button-container button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.share-button-container button:hover {
  background-color: #0056b3;
}

/* Score Emojis */
.score-emoji {
  text-align: center;
  font-size: 24px;
  margin-bottom: 10px;
  line-height: 1.5;
}

/* Word and Gap Vertical Alignment */
.word,
.gap {
  vertical-align: middle;
}

/* Additional Styles for Colored States */
.word.correct {
  background-color: #28a745 !important; /* Green */
}

.word.wrong-place {
  background-color: #fd7e14 !important; /* Orange */
}

.word.incorrect {
  background-color: #dc3545 !important; /* Red */
}

.word.unused {
  background-color: #007bff !important; /* Blue */
}

/* Responsive Styles */
@media (max-width: 600px) {
  .analogies {
    padding: 5px;
    font-size: 14px;
  }

  .word-cloud {
    padding: 5px;
    font-size: 14px;
  }

  .word-cloud .word {
    padding: 4px 6px;
    margin: 3px;
    font-size: 12px;
  }

  .gap {
    padding: 4px 6px;
    min-width: 50px;
    font-size: 12px;
  }

  .game-container {
    transform: scale(calc(100vw / 800)); /* Scale down dynamically on smaller screens */
  }
  
}

/* Add this to your CSS file */

.share-button-container button.copied {
  background-color: #28a745; /* Green */
}

.share-button-container button.copied:hover {
  background-color: #218838; /* Darker green on hover */
}

